<template>
	<div id="seller">
		<div class="my-bar">
			<div class="my-bar-left">
				<div class="arrow-left-con">
					<van-icon name="arrow-left" @click.native="$router.go('-1')"/>
				</div>
			</div>
		</div>

    <div class="my-bar" style="right: 60px">
      <div class="my-bar-left">
        <img src="static/img/icon_ss_sjzy.png" width="22px" @click="toSearchSeller"/>
      </div>
    </div>
    <div class="my-bar" style="right: 10px">
      <div class="my-bar-left">
        <img src="static/img/icon_fx_sjzy.png" width="22px" @click="copeText"/>
      </div>
    </div>
		<van-swipe class="goods-swipe" :autoplay="3000">
			<van-swipe-item>
				<img :src="seller.shoplogo" width="100%" height="240px">
			</van-swipe-item>
		</van-swipe>

    <div class="background_color_ffffff shopHead">
      <a @click="tel(seller.tel)">
        <img src="static/img/icon_1111.png" width="30px" style="margin-right: 20px;"/>
      </a>
      <div style="display: inline-block;" @click="toChat">
        <img :src="seller.shoplogo" width="50px" style="vertical-align: middle;"/>
      </div>
      <div style="display: inline-block;margin-left: 10px;">
        <div class="font_weight_bold">{{seller.shopname}}</div>
        <van-rate v-model="value"/>
      </div>
      <img src="static/img/icon_ygz_sjzy.png" width="30px" style="position: absolute;right: 10px;" @click="colShop" v-show="show"/>
      <img src="static/img/icon_sc_sjzy.png" width="30px" style="position: absolute;right: 10px;" v-show="!show" @click="removeShop">
    </div>

    <van-list v-model="loading" :finished="finished" @load="getSellerInfo">
      <van-card v-for="(item, index) in sellerInfo" :key="index" style="margin: 10px;border-radius: 5px" @click.native="toGoods(item)">
        <div slot="thumb">
          <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" width="100%" style="display: block;border-radius: 3px"/>
        </div>
        <div slot="tags" class="font_weight_bold padding_bottom_5">
          {{item.goodsname}}
        </div>
        <div slot="tags" class="font_12 color_8b8b8b" style="max-height: 44px;height: 44px;">
          <span style="height: 36px;overflow: hidden;text-overflow: ellipsis;width: 100%;line-height: 14px;display: block;">{{item.introduce}}</span>
        </div>
        <div slot="tags" class="font_12" style="padding-top: 10px;">
          <span class="color_c10804" v-if="item.type==1">¥{{item.price}}</span>
          <span class="color_c10804" v-if="item.type==2">¥{{item.spike}}</span>
          <span class="float_right color_8b8b8b">剩余&nbsp;&nbsp;&nbsp;&nbsp;{{item.num}}</span>
        </div>
      </van-card>
    </van-list>
	</div>
</template>

<script>
	export default {
		name: 'seller',
		data() {
			return {
			  seller:[],
        sellerInfo:[],
        value:2,
        goodsList: [],
        show:true,
        userId:localStorage.getItem('userId'),
        loading: false,
        finished: false,
        pageNum: 1,
			}
		},
    mounted() {
      this.seller = this.$store.state.sellerInfo;
      if(this.seller.isfollow=='Y'){
        this.show = false;
      }
      console.log(this.seller);
      this.getSellerInfo();
      this.getSeller();
    },
		methods: {
      tel(telNum){
        dsBridge.call('dialingTel',telNum);
      },
      toChat(){
        let chat = {
          shopid:this.sellerInfo.shopid,
          shoplogo:this.sellerInfo.shoplogo,
          shopname:this.sellerInfo.shopname,
        };
        this.$router.push({path:'chat',query:{data:chat}});
      },
      getSeller(){
        let $this = this;
        let qs = require('qs');
        let data = {
          shopid:this.seller.shopid,
          userid:this.userId,
        };
        $this.$ajax.post("shop/getshopinfo", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.seller = rpdata.data.data;
            if($this.seller.isfollow=='Y'){
              $this.show = false;
            }
          }
        })
        .catch(function(err) {})
      },
      removeShop(){
        let $this = this;
        let qs = require('qs');
        let data = {
          shopid:this.seller.shopid,
          userid:this.userId,
        };
        $this.$ajax.post("follow/canclefollow", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '取消关注成功!',
              position: "bottom"
            });
            $this.show = true;
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      colShop(){
        let $this = this;
        let qs = require('qs');
        let data = {
          shopid:this.seller.shopid,
          userid:this.userId,
        };
        $this.$ajax.post("follow/addfollow", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '关注店铺成功!',
              position: "bottom"
            });
            $this.show = false;
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      toSearchSeller(){
        this.$router.push('searchSeller');
      },
      toGoods(dataMap){
        if(dataMap.type==1){
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goods');
        }else{
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goodsKill');
        }
      },
      getSellerInfo(){
        this.loading = true;
        let $this = this;
        let qs = require('qs');
        let data = {
          shopid:this.seller.shopid,
          userid:this.userId,
          pageNum: this.pageNum
        };
        $this.$ajax.post("goods/getgoodslist", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.sellerInfo = $this.sellerInfo.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
            // $this.sellerInfo = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
      copeText(){
        dsBridge.call('shareUrl','https://www.pgyer.com/DRwB');
      },
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .shopHead{
    width: 80%;
    position: relative;
    bottom: 25px;
    padding: 10px 10px;
    margin-left: 8%;
    display: flex;
    align-items: center;
    border-radius: 5px;
  }
</style>



